"use client";

import * as React from "react";
import Link, { type LinkProps } from "next/link";
import { type BadgeProps, Badge } from "@/components/ui/primitives/Badge";
import { cn, withBasePath } from "@/lib/utils";
import { TeamList } from "./TeamList";

const CHIP_CLASSNAME = cn(
  "min-w-0 max-w-full truncate text-left overflow-hidden text-ellipsis",
  "hover:bg-muted/28",
  "focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 focus-visible:ring-offset-background",
  "transition-colors duration-motion-sm ease-out motion-reduce:transition-none",
);

const TOOLTIP_CLASSNAME = cn(
  "pointer-events-none absolute left-1/2 top-full z-20 w-max max-w-[min(var(--layout-quick-action-tooltip-max),calc(100vw-var(--space-6)))] -translate-x-1/2",
  "rounded-[var(--radius-lg)] border border-card-hairline bg-[hsl(var(--surface-2))] px-[var(--space-3)] py-[var(--space-2)]",
  "text-label font-medium text-foreground shadow-depth-soft",
  "transition-[opacity,transform] duration-motion-sm ease-out motion-reduce:transition-none motion-reduce:transform-none",
);

type TeamQuickActionHref = LinkProps["href"];

type TeamQuickAction = {
  id?: string;
  href: TeamQuickActionHref;
  label: string;
  tone?: BadgeProps["tone"];
};

type TeamQuickActionsProps = {
  actions: TeamQuickAction[];
  className?: string;
};

type TooltipState = {
  id: string;
};

export function TeamQuickActions({
  actions,
  className,
}: TeamQuickActionsProps) {
  const listId = React.useId();
  const [tooltip, setTooltip] = React.useState<TooltipState | null>(null);

  const closeTooltip = React.useCallback(() => {
    setTooltip(null);
  }, []);

  React.useEffect(() => {
    if (!tooltip) {
      return;
    }

    const handleKeyDown = (event: KeyboardEvent) => {
      if (event.key === "Escape" || event.key === "Esc") {
        closeTooltip();
      }
    };

    window.addEventListener("keydown", handleKeyDown);
    return () => window.removeEventListener("keydown", handleKeyDown);
  }, [tooltip, closeTooltip]);

  if (actions.length === 0) {
    return null;
  }

  return (
    <TeamList
      items={actions}
      className={className}
      getKey={(action, index) => {
        const { id, href } = action;
        if (typeof href === "string") {
          const trimmedHref = href.trim();
          if (id) return id;
          if (trimmedHref) return trimmedHref;
          return `${index}`;
        }
        if (id) return id;
        try {
          return JSON.stringify(href) ?? `${index}`;
        } catch {
          return `${index}`;
        }
      }}
      renderItem={(action, { index }) => {
        const { href, label, tone = "neutral" } = action;
        const hrefIsString = typeof href === "string";
        const trimmedHref = hrefIsString ? href.trim() : "";
        const actionId = `${listId}-${index}`;
        const tooltipId = `${actionId}-tooltip`;
        const isTooltipOpen = tooltip?.id === actionId;

        const resolvedHref: TeamQuickActionHref = hrefIsString
          ? withBasePath(trimmedHref, { skipForNextLink: true })
          : typeof href === "object" &&
              href !== null &&
              "pathname" in href &&
              typeof href.pathname === "string"
            ? {
                ...href,
                pathname: withBasePath(href.pathname, { skipForNextLink: true }),
              }
            : href;

        const handleOpen = () => {
          setTooltip({ id: actionId });
        };

        const handleClose = (
          event:
            | React.FocusEvent<HTMLAnchorElement>
            | React.MouseEvent<HTMLAnchorElement>,
        ) => {
          const target = event.currentTarget;
          if (
            event.type === "mouseleave" &&
            target instanceof HTMLElement &&
            target === document.activeElement
          ) {
            return;
          }
          setTooltip((prev) => (prev?.id === actionId ? null : prev));
        };

        const handleKeyDown: React.KeyboardEventHandler<HTMLAnchorElement> = (
          event,
        ) => {
          if (event.key === "Escape" || event.key === "Esc") {
            closeTooltip();
          }
        };

        return (
          <div className="relative">
            <Badge
              as={Link}
              href={resolvedHref}
              tone={tone}
              size="lg"
              interactive
              className={cn(
                "flex min-w-[min(100%,var(--layout-quick-action-min))] max-w-full justify-start whitespace-nowrap",
                CHIP_CLASSNAME,
              )}
              aria-describedby={isTooltipOpen ? tooltipId : undefined}
              onFocus={handleOpen}
              onBlur={handleClose}
              onMouseEnter={handleOpen}
              onMouseLeave={handleClose}
              onKeyDown={handleKeyDown}
            >
              {label}
            </Badge>
            <div
              id={tooltipId}
              role="tooltip"
              className={cn(
                TOOLTIP_CLASSNAME,
                isTooltipOpen
                  ? "opacity-100 translate-y-[var(--space-2)]"
                  : "pointer-events-none opacity-0 -translate-y-[var(--space-1)]",
              )}
              aria-hidden={isTooltipOpen ? undefined : "true"}
            >
              <span className="block text-balance text-left text-[length:var(--font-label)] leading-tight">
                {label}
              </span>
            </div>
          </div>
        );
      }}
    />
  );
}

export type { TeamQuickAction };
